//全局注册过滤器
Vue.filter("formatDate", (value, format) => {
    // 
    // 
    let date = new Date(value);
    // 
    // let year = date.getFullYear();
    // if (/(y+)/.test(format)) {
    // 获取匹配的字符串
    //     let content = RegExp.$1
    //修改年的字符串数
    // format = format.replace(content, year.toString().slice(4 - content.length)); format = format.replace(content, year.toString().slice(4 - content.length));
    // }
    let o = {
        y: date.getFullYear(),
        M: date.getMonth() + 1,
        d: date.getDate(),
        h: date.getHours(),
        m: date.getMinutes(),
        s: date.getSeconds(),
    }
    for (let key in o) {
        // 创建动态正则
        let reg = new RegExp(`(${key}+)`)
        if (reg.test(format)) {
            let content = RegExp.$1;
            format = format.replace(content, o[key].toString().padStart(content.length, "0"))
                // 

        }
    }
    return format

})

Vue.config.productionTip = false
