export const routes = [{
        path: '/login',
        name: 'Login',
        component: () =>
            import ('../views/Login.vue'),
    },
    {
        path: '/main',
        name: 'Main',
        component: () =>
            import ('../views/Main.vue'),
        children: [{
                path: '/',
                name: 'Home',
                component: () =>
                    import ('../views/mainviews/Home.vue')
            },
            {
                path: 'menu',
                name: 'Menu',
                component: () =>
                    import ('../views/mainviews/Menu.vue')
            },
            {
                path: 'shopbag',
                name: 'Shopbag',
                component: () =>
                    import ('../views/mainviews/Shopbag.vue')
            },
            {
                path: 'my',
                name: 'My',
                component: () =>
                    import ('../views/mainviews/My.vue')

            },
            {
                path: '*',
                redirect: {
                    name: "Home"
                }
            }
        ]
    },
    {
        path: '/detail',
        name: 'Detail',
        component: () =>
            import ('../views/Detail.vue'),
    },
    {
        path: '/pay',
        name: 'Pay',
        component: () =>
            import ('../views/Pay.vue'),
    },
    {
        path: '/newaddress',
        name: 'Newaddress',
        component: () =>
            import ('../views/Newaddress.vue'),
    },
    {
        path: '/manageaddress',
        name: 'Manageaddress',
        component: () =>
            import ('../views/Manageaddress.vue'),
    },
    {
        path: '/account',
        name: 'Account',
        component: () =>
            import ('../views/Account.vue'),
    },
    {
        path: '/order',
        name: 'Order',
        component: () =>
            import ('../views/Order.vue'),



    },
    {
        path: '/mylive',
        name: 'Mylive',
        component: () =>
            import ('../views/Mylive.vue'),
    },
    {
        path: '/security',
        name: 'Security',
        component: () =>
            import ('../views/Security.vue'),
    },
    {
        path: '/forgotpw',
        name: 'Forgotpw',
        component: () =>
            import ('../views/Forgotpw.vue'),
    },
    {
        path: '*',
        redirect: {
            name: "Home"
        }
    }

]